<template>
    <div style="margin-top: 100px">
        <div class="center">
            <p-progress-spinner />
        </div>
        <div class="center">
            <h3>
                You are trying to login from Outside Network. Verification Link
                will be sent by SMS to your phone...
            </h3>
        </div>
    </div>
</template>

<script>
import { $axios } from "@/service/app-http";
import { onUnmounted } from "vue";
import { useRouter } from "vue-router";
import { useStore } from "vuex";

export default {
    setup() {
        console.log();
        const store = useStore();
        const router = useRouter();

        const verify = setInterval(() => {
            $axios
                .post(
                    "https://cycles.nlf.clinic/wsl-cycles/public/token-status",
                    {
                        token: store.state.user.accessToken
                    }
                )
                .then((resp) => {
                    localStorage.setItem("needSecondVerification", false);
                    store.state.needSecondVerification = false;
                    router.push("/art-cycle");
                })
                .catch((err) => {
                    if (err.response.status != 403) {
                        router.push("/login");
                    }
                });
        }, 5000);

        onUnmounted(() => {
            clearInterval(verify);
        });
    }
};
</script>

<style scoped>

.center {
    display: flex;
    align-items: center;
    justify-content: center;
}
</style>
